import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    todoList: [
      {
        title: 'first todo',
        completed: false,
        work_time: 1500,
        break_time: 300,
        pomodoro: 0,
        date: '2019/08/14',
        play: false,
        id: 1,
        timer: null
      },
      {
        title: 'second todo',
        completed: false,
        work_time: 1500,
        break_time: 300,
        pomodoro: 0,
        date: '2019/08/14',
        play: false,
        id: 2,
        timer: null
      },
      {
        title: 'third todo',
        completed: false,
        work_time: 1500,
        break_time: 300,
        pomodoro: 0,
        date: '2019/08/14',
        play: false,
        id: 3,
        timer: null
      },
      {
        title: 'forth todo',
        completed: false,
        work_time: 1500,
        break_time: 300,
        pomodoro: 0,
        date: '2019/08/14',
        play: false,
        id: 4,
        timer: null
      }
    ],
    work_rings: [
      {
        title: 'none',
        selected: true
      },
      {
        title: 'alerm',
        selected: false
      },
      {
        title: 'alert',
        selected: false
      },
      {
        title: 'beep',
        selected: false
      },
      {
        title: 'bugle',
        selected: false
      },
      {
        title: 'digital',
        selected: false
      },
      {
        title: 'ring',
        selected: false
      },
      {
        title: 'whistle',
        selected: false
      }
    ],
    break_rings: [
      {
        title: 'none',
        selected: true
      },
      {
        title: 'alerm',
        selected: false
      },
      {
        title: 'alert',
        selected: false
      },
      {
        title: 'beep',
        selected: false
      },
      {
        title: 'bugle',
        selected: false
      },
      {
        title: 'digital',
        selected: false
      },
      {
        title: 'ring',
        selected: false
      },
      {
        title: 'whistle',
        selected: false
      }
    ],
    speed: 1000,
    isBreak: false,
    boardNow: '',
    work_ring: 'none',
    break_ring: 'none'
  },
  mutations: {
    UPDATETODOLIST (state, payload) {
      const isNew = state.todoList.findIndex(todo => todo.id === payload.id)
      if (isNew === -1) {
        state.todoList.push(payload)
      } else {
        state.todoList[isNew] = payload
      }
    },
    CHANGESTATE (state) {
      state.isBreak = !state.isBreak
    },
    SIWTCHTODO (state, payload) {
      const first = state.todoList.findIndex(todo => !todo.completed)
      const temp = state.todoList[first]
      const sw = state.todoList.findIndex(todo => todo.id === payload.id)
      state.todoList[first] = payload
      state.todoList[sw] = temp
      // 為了讓 getter 運作，所以對陣列進行新增、刪除
      state.todoList.push({})
      state.todoList.pop()
    },
    SETBOARD (state, payload) {
      state.boardNow = payload
    },
    SETRING (state, payload) {
      const type = payload.type
      state[type.slice(0, -1)] = payload.ring.title
      state[type].forEach(ring => {
        if (ring.title === payload.ring.title) {
          ring.selected = true
        } else {
          ring.selected = false
        }
      })
    }
  },
  actions: {
    addTodo (context, payload) {
      const title = payload.target.value
      payload.target.value = ''
      const data = {
        title,
        completed: false,
        work_time: 1500,
        break_time: 300,
        pomodoro: 0,
        date: new Date().toLocaleDateString('chinese'),
        play: false,
        id: Math.floor(Date.now() / 1000),
        timer: null
      }
      context.commit('UPDATETODOLIST', data)
    },
    playTodo (context, todo) {
      todo.play = !todo.play
      context.commit('UPDATETODOLIST', todo)
      if (todo.play) {
        todo.timer = setInterval(() => {
          if (context.state.isBreak) {
            this.dispatch('break', todo)
          } else {
            this.dispatch('work', todo)
          }
          context.commit('UPDATETODOLIST', todo)
        }, context.state.speed)
      } else {
        clearInterval(todo.timer)
        context.commit('UPDATETODOLIST', todo)
      }
    },
    work (context, todo) {
      todo.work_time--
      if (todo.work_time === 0) {
        clearInterval(todo.timer)
        if (context.state.work_ring !== 'none') {
          let audio = new Audio(require(`../assets/music/${context.state.work_ring}.mp3`))
          audio.play()
          setTimeout(() => audio.pause(), 2000)
        }
        todo.play = false
        todo.pomodoro++
        todo.work_time = 1500
        document.documentElement.style.setProperty('--primary', '#00A7FF')
        context.commit('CHANGESTATE')
      }
    },
    break (context, todo) {
      todo.break_time--
      if (todo.break_time === 0) {
        clearInterval(todo.timer)
        if (context.state.break_ring !== 'none') {
          let audio = new Audio(require(`../assets/music/${context.state.break_ring}.mp3`))
          audio.play()
          setTimeout(() => audio.pause(), 2000)
        }
        todo.play = false
        todo.break_time = 300
        document.documentElement.style.setProperty('--primary', '#FF4384')
        context.commit('CHANGESTATE')
      }
    }
  },
  getters: {
    AllList (state) {
      return state.todoList.filter(todo => !todo.completed)
    },
    TodoList (state) {
      const temp = state.todoList.filter(todo => !todo.completed)
      return temp.slice(1, 4)
    },
    Todo (state) {
      return state.todoList.filter(todo => !todo.completed)[0]
    },
    DoneList (state) {
      return state.todoList.filter(todo => todo.completed)
    },
    Isbreak (state) {
      return state.isBreak
    },
    Board (state) {
      return state.boardNow
    },
    WorkRing (state) {
      return state.work_rings
    },
    BreakRing (state) {
      return state.break_rings
    }
  }
})
